body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background-image:url(/static/media/background2.702d8be9.png);
  background-position: top center;
  min-height: 668px;
  background-size: cover;
  background-attachment: fixed;
  font-family: 'Arimo', sans-serif;
}
.menu-bg{

  background: #f2f2f2;
}
.toggle-bg{

  background: #c7c9e4ce;
}
.menu {
  width:100%;
  text-align:center;
  }



.menu div a{
  text-decoration: none;
  padding: 12px 5px;
  margin: 5px 20px;
  font-size: 18px;
  font-weight: 900;
  color: rgb(68, 68, 68) !important;
  cursor: pointer;
  text-shadow: none !important;


}
.menu div a:hover{
  /* font-size: 18.5px; */
  /* margin: 5px 19px; */
  color: rgb(99, 99, 99) !important;
}
.menu div a.active{
  border-bottom: 1px solid #444444;
  color: rgb(99, 99, 99) !important;

  /* font-size: 18.5px; */
  /* margin: 5px 19px; */
}
.logo{
  width: 100%;
  text-align: center;
  margin-left: 20px;
}
.center{

  width: 100%;
  height: 100vh;
  text-align: center;
  margin-top: 250px;
  font-size: 100px;
  color: darkslategray;
}

.darkspottext {
  color: rgb(68, 68, 68);
  font-size: 43px;
  font-weight: 400;
  line-height: 1.60em;
  text-transform: uppercase;
  margin-bottom: 10px;
}
.darksmallspottext {
  font-size: 22px;
  color: rgba(68, 68, 68, 0.65);
  line-height: 1.2em;
  text-transform: uppercase;
}
.spot .spot_text{
  position: absolute;
  top: 50%;
  left: 10%;

}

.spot {
  width: 100%;
}

.spot .spot_img{
  position: absolute;
  top: 15%;
  right: 10%;
}

.spot .down_icon{
  width: 20px;
  margin: 90vh auto 0 auto;
}

.spot .spot_img img{
  height: 350px;
  -webkit-transition: 0.7s;
  transition: 0.7s;
}

.spot .spot_img img:hover{
  height: 500px;
  -webkit-transition: 2s;
  transition: 2s;

}
.spot .down_icon i{
  font-size: 30px;
  cursor: pointer;
  color: #444444;
}

@media all and (max-width : 950px) {
  .spot .spot_text{
  margin: 100px auto;
    position: static;
  }
  .spot {
    text-align: center;
  
  }
  .spot .spot_img{
    margin: auto;
    position: static;
  }
  .spot .down_icon{
    width: 20px;
    margin: 40px auto 0 auto;
  }

  .spot .spot_img{
    display: none;
  }
}
@media all and (max-width : 500px) {
  #section1, .link_to_section1, .down_icon {
    display: none;
  }
}

